import 'uni-pages';import Vue from 'vue'
import App from './App'

//全局域名
Vue.prototype.$base = "https://h5.xiaocantong.net/"
import uView from "uview-ui";
Vue.use(uView);
import store from './store/index';
Vue.prototype.$store = store;

//配置
import config from './config.js'
Vue.prototype.$conf = config 


Vue.prototype.$msg = (title, duration=1500, mask=false, icon='none')=>{
	//统一提示方便全局修改
	if(Boolean(title) === false){
		return;
	}
	uni.showToast({
		title,
		duration,
		mask,
		icon
	});
}

import api from './common/vmeitime-http/index.js'
Vue.prototype.$api = api

import appupgrade from './common/appupgrade.js'
Vue.prototype.$appUpgrade = appupgrade

Vue.config.productionTip = false

App.mpType = 'app'

const app = new Vue({
    ...App
})
// http拦截器，此为需要加入的内容，如果不是写在common目录，请自行修改引入路径
import httpInterceptor from '@/common/http.interceptor.js'
// 这里需要写在最后，是为了等Vue创建对象完成，引入"app"对象(也即页面的"this"实例)
Vue.use(httpInterceptor, app)

app.$mount()